@import url(https://fonts.googleapis.com/css?family=Droid+Sans+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    margin: 0;
}

header {
    display: flex;
    position: relative;
}

header h1 {
    font-weight: lighter;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: #000000;
}

.flipClock {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 3vw;
}

.flipUnitContainer {
    display: block;
    position: relative;
    width: 18vw;
    margin-right: 2vw;
    margin-left: 2vw;
    height: 18vw;
    perspective-origin: 50% 50%;
    perspective: 300px;
    background-color: #dee3e6;
    border-radius: 2.5vw;
    box-shadow: 0 3px 10px #000000;
    border-top: 1px solid #000000;
}

.upperCard, .lowerCard {
    display: flex;
    position: relative;
    justify-content: center;
    width: 100%;
    height: 50%;
    overflow: hidden;
    border: 1px solid #d3d2c4;
}

.upperCard span, .lowerCard span {
    font-size: 10vw;
    /* font-family: cursive; */
    font-weight: lighter;
    color: black;
}

.upperCard {
    align-items: flex-end;
    border-bottom: 0.5px solid #d3d2c4;
    border-top-left-radius: 2.5vw;
    border-top-right-radius: 2.5vw;
}

.upperCard span {
    transform: translateY(50%);
}

.lowerCard {
    align-items: flex-start;
    border-top: 0.5px solid #d3d2c4;
    border-bottom-left-radius: 2.5vw;
    border-bottom-right-radius: 2.5vw;
}

.lowerCard span {
    transform: translateY(-50%);
}

.flipCard {
    display: flex;
    justify-content: center;
    position: absolute;
    left: 0;
    width: 100%;
    height: 50%;
    overflow: hidden;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.flipCard span {
    /* font-family: cursive; */
    font-size: 10vw;
    font-weight: lighter;
    color: black;
}

.flipCard.unfold {
    top: 50%;
    align-items: flex-start;
    transform-origin: 50% 0%;
    transform: rotateX(180deg);
    background-color: #dee3e6;
    border-bottom-left-radius: 2.5vw;
    border-bottom-right-radius: 2.5vw;
    border: 0.1vw solid #d3d2c4;
    border-top: 0.1vw solid #d3d2c4;
}

.flipCard.unfold span {
    transform: translateY(-50%);
}

.flipCard.fold {
    top: 0%;
    align-items: flex-end;
    transform-origin: 50% 100%;
    transform: rotateX(0deg);
    background-color: #dee3e6;
    border-top-left-radius: 2.5vw;
    border-top-right-radius: 2.5vw;
    border: 0.5px solid #d3d2c4;
    border-bottom: 0.5px solid #d3d2c4;
}

.flipCard.fold span {
    transform: translateY(50%);
}

.fold {
    -webkit-animation: fold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;
    animation: fold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;
    transform-style: preserve-3d;
}

.unfold {
    -webkit-animation: unfold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;
    animation: unfold 0.6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;
    transform-style: preserve-3d;
}

.flipCard span, .upperCard span, .lowerCard span {
    font-family: 'Architects Daughter', cursive, 'Fredericka the Great', cursive, 'Josefin Slab', serif, 'Open Sans Condensed', sans-serif;
}

.h1 {
    font-family: 'Architects Daughter', cursive, 'Fredericka the Great', cursive, 'Josefin Slab', serif, 'Open Sans Condensed', sans-serif;
}

.upperCard, .flipCard.unfold, .flipCard.fold {
    background-color: #dee3e6;
}

@-webkit-keyframes fold {
    0% {
        transform: rotateX(0deg);
    }
    100% {
        transform: rotateX(-180deg);
    }
}

@keyframes fold {
    0% {
        transform: rotateX(0deg);
    }
    100% {
        transform: rotateX(-180deg);
    }
}

@-webkit-keyframes unfold {
    0% {
        transform: rotateX(180deg);
    }
    100% {
        transform: rotateX(0deg);
    }
}

@keyframes unfold {
    0% {
        transform: rotateX(180deg);
    }
    100% {
        transform: rotateX(0deg);
    }
}

@media screen and (min-width: 576px) {
    .flipCard span, .upperCard span, .lowerCard span {
        font-size: 9vw;
        font-family: 'Architects Daughter', cursive, 'Fredericka the Great', cursive, 'Josefin Slab', serif, 'Open Sans Condensed', sans-serif;
    }

    .flipUnitContainer {
        height: 16vw;
        width: 16vw;
    }
}

/* Medium devices (tablets, 768px and up)  */

@media screen and (min-width: 768px) {
    .flipCard span, .upperCard span, .lowerCard span {
        font-size: 8vw;
    }
    .flipUnitContainer {
        height: 13vw;
        width: 13vw;
    }
    .flipClock{
        margin-bottom: 2vw;
    }
}

/* Large devices (desktops, 992px and up) */

@media screen and (min-width: 992px) {
    .flipCard span, .upperCard span, .lowerCard span {
        font-size: 9vw;
    }
    .flipUnitContainer {
        height: 12vw;
        width: 12vw;
    }
    .flipClock{
        margin-bottom: 1vw;
    }
}

/* Extra large devices (large desktops, 1200px and up) */

@media (min-width: 1200px) {
    .flipCard span, .upperCard span, .lowerCard span {
        font-size: 6vw;
    }
    .flipUnitContainer {
        height: 10vw;
        width: 10vw;
    }
    .flipClock{
        margin-bottom: 0;
    }
}

 /* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) { 

    .flipCard span, .upperCard span, .lowerCard span {
        font-size: 4.5vw;
    }
    .flipUnitContainer {
        height: 8vw;
        width: 8vw;
    }
    .flipClock{
        margin-bottom: 0;
    }
  }
